import Vue from 'vue'
import Router from 'vue-router'
import TakeOutFood from '../pages/take-out-food'
import Search from '../pages/search'
import Order from '../pages/order'
import PersonalCenter from '../pages/personal-center'
import Business from '../pages/business'
import Shops from '../pages/shops'
import Positioning from '../pages/positioning'
import positionSearch from '../pages/position-search'
import InfoShop from '../pages/info-shop';
Vue.use(Router)

export default new Router({
  routes: [
	{
		path:'/take-out-food',
		name:'TakeOutFood',
		component:TakeOutFood
	},
	{
		path:'/search',
		name:'Search',
		component:Search
	},
	{
		path:'/shops',
		name:'Shops',
		component:Shops,
		children:[
			{
				path:':id',
				component:Shops,
			}
		]
	},
	{
		path:'/order',
		name:'Order',
		component:Order
	},
	{
		path:'/personal-center',
		name:'PersonalCenter',
		component:PersonalCenter
	},
	{
		path:'/business',
		name:'Business',
		component:Business
	},
	{
		path:'/positioning',
		name:'Positioning',
		component:Positioning
	},
	{
		path:'/position-search',
		name:'positionSearch',
		component:positionSearch
	},
	{
		path:'/info-shop',
		name:'InfoShop',
		component:InfoShop
	},
	{
		path:'*',
		redirect:'/take-out-food'
	}
  ]
})



